import "./featuredInfo.css";
import { ArrowDownward, ArrowUpward } from "@material-ui/icons";
import { useEffect, useState } from "react";
import { userRequest } from "../../requestMethods";


export default function FeaturedInfo() {

  const [ stats , setStats ] = useState([]);
  const [ perc , setPerc ] = useState(0);

  useEffect(() => {
      const getIncome = async () => {
        try{
            const res = await userRequest.get("/users/stats");    
            setStats(res.data);
            setPerc((100 - res.data[0].total*100) / (res.data[0].total)  );
        } catch{}
      }

      getIncome();
      
  }, [])

  return (
    <div className="featured">
      <div className="featuredItem">
        <span className="featuredTitle">Utilisateurs</span>
        <div className="featuredMoneyContainer">
          <span className="featuredMoney">{stats[0]?.total}</span>
        
          <span className="featuredMoneyRate">
            +11.4 <ArrowUpward  className="featuredIcon positive"/>
          </span>
        </div>
        <span className="featuredSub">Comparaison par rapport au dernier mois</span>
      </div>
      <div className="featuredItem">
        <span className="featuredTitle">Ventes</span>
        <div className="featuredMoneyContainer">
          <span className="featuredMoney">4.415</span>
          <span className="featuredMoneyRate">
            %{Math.floor(perc)}{" "} 
            { perc < 0 ? (<ArrowDownward className="featuredIcon negative"/>):(<ArrowUpward className="featuredIcon positive"/>)
            }
          </span>
        </div>
        <span className="featuredSub">Comparaison par rapport au dernier mois</span>
      </div>
      <div className="featuredItem">
        <span className="featuredTitle">Cout</span>
        <div className="featuredMoneyContainer">
          <span className="featuredMoney">2.225</span>
          <span className="featuredMoneyRate">
            +2.4 <ArrowUpward className="featuredIcon"/>
          </span>
        </div>
        <span className="featuredSub">Comparaison par rapport au dernier mois</span>
      </div>
    </div>
  );
}
